<template>
  <v-container :class="{ 'px-0': $vuetify.breakpoint.mdAndDown }" v-scroll="scroll">
    <v-layout text-xs-center wrap>
      <v-flex d-flex xs12 sm6 md4 lg3 xl2 v-for="(item, i) in items" :key="'user_card_' + i" class="pa-2">
        <v-card>
          <v-card-title class="headline">
            {{ item.name }}
          </v-card-title>
          <v-card-text class="text-xs-left">
            <dl>
              <dt>{{ $t('owner') }}</dt>
              <dd>{{ item.owner ? item.owner.name : $t('N/A') }}</dd>
              <dt>{{ $t('certification') }}</dt>
              <dd>{{ $t(`switch.${item.certification}`) }}</dd>
              <dt>{{ $t('revision period') }}</dt>
              <dd>{{ item.revisionPeriod }}</dd>
              <dt>{{ $t('last revision') }}</dt>
              <dd>{{ item.lastRevision }}</dd>
              <dt>{{ $t('last ZTE') }}</dt>
              <dd>{{ item.lastZTE }}</dd>
              <dt>{{ $t('last ZTL') }}</dt>
              <dd>{{ item.lastZTL }}</dd>
            </dl>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn flat color="primary" v-text="$t('edit')" @click="edit(i)"/>
            <v-btn flat color="primary" v-text="$t('delete')" @click="remove(i)"/>
          </v-card-actions>
        </v-card>
      </v-flex>
      <v-flex xs12 :style="{ visibility: loading ? 'visible' : 'hidden' }" class="pa-5">
        <v-progress-circular color="primary" :indeterminate="true"/>
      </v-flex>
      <CarDialog ref="dialog" :value="items[index] || {}" @input="save"/>
      <v-btn @click="edit(items.length)" color="accent" fixed dark fab bottom right>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import { getCars, postCar, putCar, deleteCar } from '@/axios'

import CarDialog from '@/components/CarDialog'
import CTable from '@/components/Table'

export default {
  components: { CTable, CarDialog },
  data: () => ({
    items: [],
    index: -1,
    loading: false
  }),
  computed: {
    itemsPerLoad () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 3
        case 'sm': return 6
        case 'md': return 9
        case 'lg': return 12
        case 'xl': return 18
        default: return 1
      }
    }
  },
  methods: {
    edit (i) {
      this.index = i
      this.$refs.dialog.show = true
    },
    async save (item) {
      this.$set(this.items, this.index, item)
      if (item.carId != null) {
        try {
          this.$store.commit('setWorking', true)
          await putCar(JSON.parse(JSON.stringify(item)))
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('car put success')
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('car put error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      } else {
        try {
          this.$store.commit('setWorking', true)
          const id = await postCar(JSON.parse(JSON.stringify(item)))
          item.carId = id
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('car post success')
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('car post error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async remove (i) {
      if (confirm(this.$t('confirm delete'))) {
        const item = this.items.splice(i, 1)[0]
        try {
          this.$store.commit('setWorking', true)
          await deleteCar(item.carId)
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('car delete success')
          })
        } catch (error) {
          this.items.splice(i, 0, item)
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('car delete error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async load () {
      this.loading = true
      try {
        const cars = await getCars(this.items.length, this.itemsPerLoad)
        if (cars.length) {
          this.items.push(...cars)
          window.setTimeout(() => {
            this.scroll()
          })
        }
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false
      }
    },
    scroll () {
      if (
        !this.loading && (
          document.documentElement.offsetHeight ===
          document.documentElement.scrollTop + window.innerHeight
        )
      ) {
        this.load()
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
