// @todo: there should be an API for this
async function searchOwners (search) {
  const { data } = await download({
    method: 'get',
    url: '/car/0/100'
  })
  const set = new Set()
  return data
    .map(({ owner }) => owner)
    .filter(v => {
      if (v == null) {
        return false
      }
      if (set.has(v.subjectId)) {
        return false
      }
      set.add(v.subjectId)
      return true
    })
}

export default axios
export {
  getCars, postCar, putCar, deleteCar,
  getUsers, postUser, putUser, deleteUser,
  searchOwners
}
