<template>
  <v-container :class="{ 'px-0': $vuetify.breakpoint.mdAndDown }">
    <v-layout text-xs-center wrap>
      <v-flex d-flex xs12 sm6 md4 lg3 xl2 v-for="(item, i) in items" :key="'user_card_' + i" class="pa-2">
        <v-card>
          <v-card-title class="headline">
            {{ `${item.firstName} ${item.lastName}` }}
          </v-card-title>
          <v-card-text class="text-xs-left">
            <dl>
              <dt>{{ $t('email') }}</dt>
              <dd>{{ item.email }}</dd>
              <dt>{{ $t('role') }}</dt>
              <dd>{{ $t(`roles.${item.role.roleId}`) }}</dd>
              <dd>{{ item.role.name }}</dd>
            </dl>
          </v-card-text>
          <v-card-actions>
            <v-spacer/>
            <v-btn flat color="primary" v-text="$t('edit')" @click="edit(i)"/>
            <v-btn flat color="primary" v-text="$t('delete')" @click="remove(i)"/>
          </v-card-actions>
        </v-card>
      </v-flex>
      <v-flex xs12 :style="{ visibility: loading ? 'visible' : 'hidden' }" class="pa-5">
        <v-progress-circular color="primary" :indeterminate="true"/>
      </v-flex>
      <UserDialog ref="dialog" :value="items[index] || createNewUser()" @input="save"/>
      <v-btn @click="edit(items.length)" color="accent" fixed dark fab bottom right>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </v-layout>
  </v-container>
</template>

<script>
import { getUsers, postUser, putUser, deleteUser } from '@/axios'

import UserDialog from '@/components/UserDialog'
import CTable from '@/components/Table'

export default {
  components: { CTable, UserDialog },
  data: () => ({
    items: [],
    index: -1,
    loading: false
  }),
  computed: {
    itemsPerLoad () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 3
        case 'sm': return 6
        case 'md': return 9
        case 'lg': return 12
        case 'xl': return 18
        default: return 1
      }
    }
  },
  methods: {
    edit (i) {
      this.index = i
      this.$refs.dialog.show = true
    },
    async save (item) {
      this.$set(this.items, this.index, item)
      if (item.userId != null) {
        try {
          this.$store.commit('setWorking', true)
          await putUser(JSON.parse(JSON.stringify(item)))
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('user put success')
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('user put error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      } else {
        try {
          this.$store.commit('setWorking', true)
          const id = await postUser(JSON.parse(JSON.stringify(item)))
          item.userId = id
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('user post success')
          })
        } catch (error) {
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('user post error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async remove (i) {
      if (confirm(this.$t('confirm delete'))) {
        const item = this.items.splice(i, 1)[0]
        try {
          this.$store.commit('setWorking', true)
          await deleteUser(item.userId)
          this.$store.commit('setAlert', {
            type: 'success',
            text: this.$t('user delete success')
          })
        } catch (error) {
          this.items.splice(i, 0, item)
          this.$store.commit('setAlert', {
            type: 'error',
            text: this.$t('user delete error')
          })
        } finally {
          this.$store.commit('setWorking', false)
        }
      }
    },
    async load () {
      this.loading = true
      try {
        const users = await getUsers(this.items.length, this.itemsPerLoad)
        if (users.length) {
          this.items.push(...users)
          window.setTimeout(() => {
            this.scroll()
          })
        }
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false
      }
    },
    scroll () {
      if (
        !this.loading && (
          document.documentElement.offsetHeight ===
          document.documentElement.scrollTop + window.innerHeight
        )
      ) {
        this.load()
      }
    },
    createNewUser () {
      return {
        role: {}
      }
    }
  },
  mounted () {
    this.load()
  }
}
</script>
