<template>
  <v-dialog v-model="show" persistent scrollable :fullscreen="$vuetify.breakpoint.xsOnly" max-width="600px" @keydown.esc="cancel()">
    <v-card>
      <v-card-title>
        <span class="headline" v-text="$t('user')"/>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex xs12 md6>
              <v-text-field :label="$t('first name')" v-model="item.firstName" required/>
            </v-flex>
            <v-flex xs12 md6>
              <v-text-field :label="$t('last name')" v-model="item.lastName" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field :label="$t('email')" v-model="item.email" required/>
            </v-flex>
            <v-flex xs12>
              <v-text-field :label="$t('password')" v-model="item.password" type="password" required/>
            </v-flex>
            <v-flex xs12>
              <v-select :label="$t('role')" :items="roles" v-model="item.role.roleId" required/>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer/>
        <v-btn color="primary" flat @click.native="cancel()">{{ $t('cancel') }}</v-btn>
        <v-btn color="primary" flat @click.native="save()">{{ $t('save') }}</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props: {
    value: Object
  },
  data: () => ({
    show: false,
    item: {
      role: {}
    }
  }),
  computed: {
    roles () {
      return [1, 2, 3, 4].map(roleId => ({
        text: this.$t(`roles.${roleId}`),
        value: roleId
      }))
    }
  },
  watch: {
    show (val) {
      if (val) {
        this.item = JSON.parse(JSON.stringify(this.value))
      }
    }
  },
  methods: {
    cancel () {
      this.$emit('cancel')
      this.show = false
    },
    save () {
      // @todo
      this.item.role.name = this.$t(`roles.${this.item.role.roleId}`)
      this.$emit('input', this.item)
      this.show = false
    }
  }
}
</script>
